import PropTypes from "prop-types";
import { useState } from "react";
// @mui
import { Box, Grid, Card, Button, Typography, Stack } from "@mui/material";
//
import { useTranslation } from "react-i18next";
import Recharge from "./Recharge";
import Cashdraw from "./Cashdraw";

// ----------------------------------------------------------------------

AccountBilling.propTypes = {
  balance: PropTypes.object,
  transactions: PropTypes.array,
};

export default function AccountBilling({ balance=0, transactions=[] }) {
  const [open, setOpen] = useState(true);
  const [open1, setOpen1] = useState(false);
  const { t } = useTranslation();

  return (
    <Grid container spacing={5}>
      <Grid item xs={12} md={7}>
        <Stack spacing={2}>
          <Card sx={{ p: 3 }}>
            <Typography
              variant="overline"
              sx={{ mb: 3, display: "block", color: "text.secondary" }}
            >
              {t("params.current_balance")}
            </Typography>
            <Typography variant="h4">{balance} USD</Typography>
            <Box
              sx={{
                mt: { xs: 2, sm: 0 },
                position: { sm: "absolute" },
                top: { sm: 24 },
                right: { sm: 24 },
              }}
            >
              <Button
                size="small"
                variant="contained"
                onClick={() => {
                  setOpen(!open);
                  setOpen1(false);
                }}
                sx={{ mr: 1 }}
              >
                {t("params.deposit")}
              </Button>
            </Box>
          </Card>

          <Recharge isOpen={open} onCancel={() => setOpen(false)} />

          {/* <Cashdraw isOpen={open1} onCancel={() => setOpen1(false)} /> */}

          {/* <AccountBillingAddressBook addressBook={addressBook} /> */}
        </Stack>
      </Grid>

      {/* <Grid item xs={12} md={5}>
        <AccountBillingInvoiceHistory transactions={transactions} />
      </Grid> */}
    </Grid>
  );
}
