import * as Yup from "yup";
import PropTypes from "prop-types";
// form
import { useForm } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";
// @mui
import { Box, Card, Stack, Button, Collapse, Typography } from "@mui/material";
import { LoadingButton } from "@mui/lab";
import { useSnackbar } from "notistack";
// components
import {
  FormProvider,
  RHFSelect,
  RHFTextField,
  RHFCardField, // Create this custom field for card inputs
} from "../../../../components/hook-form";
import useIsMountedRef from "../../../../hooks/useIsMountedRef";
import { useTranslation } from "react-i18next";
import { useDispatch, useSelector } from "../../../../redux/store";
import {
  postRecharge,
  startLoading,
  stopLoading,
} from "../../../../redux/slices/wallet";
// ----------------------------------------------------------------------

Recharge.propTypes = {
  isOpen: PropTypes.bool,
  onCancel: PropTypes.func,
};

export default function Recharge({ isOpen, onCancel }) {
  const dispatch = useDispatch();
  const isMountedRef = useIsMountedRef();
  const { enqueueSnackbar } = useSnackbar();
  const { t } = useTranslation();

  const { isLoading } = useSelector((state) => state.wallet);

  // Extend the schema to validate card inputs
  const UpdateUserSchema = Yup.object().shape({
    method: Yup.string().required("Payment method is required"),
    montant: Yup.number().required("Amount is required"),
    cardNumber: Yup.string().when("method", {
      is: "Credit Card",
      then: Yup.string()
        .matches(/^\d{16}$/, "Invalid card number")
        .required("Card number is required"),
    }),
    expiryDate: Yup.string().when("method", {
      is: "Credit Card",
      then: Yup.string().matches(/^(0[1-9]|1[0-2])\/([0-9]{2})$/, "Invalid expiry date").required("Expiry date is required"),
    }),
    cvv: Yup.string().when("method", {
      is: "Credit Card",
      then: Yup.string().matches(/^\d{3}$/, "Invalid CVV").required("CVV is required"),
    }),
  });

  const defaultValues = {
    method: "Credit Card",
    montant: 10,
    cardNumber: "",
    expiryDate: "",
    cvv: "",
  };

  const methods = useForm({
    resolver: yupResolver(UpdateUserSchema),
    defaultValues,
  });

  const {
    watch,
    setError,
    handleSubmit,
    formState: { isSubmitting },
  } = methods;

  const values = watch();

  const onSubmit = async (data) => {
    enqueueSnackbar("Very Soon!", { variant: "warning" });
    // dispatch(startLoading());
    // try {
    //   const res = await postRecharge(data);
    //   enqueueSnackbar("Redirect to payment gateway!");
    //   dispatch(stopLoading());
    //   window.location.replace(res.redirect_url);
    // } catch (error) {
    //   console.error(error.response.data);
    //   enqueueSnackbar(error?.response?.data, { variant: "error" });
    //   dispatch(stopLoading());
    //   if (isMountedRef.current) {
    //     setError("montant", {
    //       ...error,
    //       message: error.response.data,
    //     });
    //   }
    // }
  };

  return (
    <Collapse in={isOpen}>
      <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
        <Card sx={{ p: 3 }}>
          <Typography
            variant="overline"
            sx={{ mb: 3, display: "block", color: "text.secondary" }}
          >
            {t("params.recharge_account")}
          </Typography>
          <Box
            sx={{
              padding: 3,
              marginTop: 3,
              borderRadius: 1,
            }}
          >
            <Stack spacing={3}>
              <Stack spacing={2} direction={{ xs: "column", md: "row" }}>
                <RHFSelect
                  name="method"
                  label={t("params.payment_method")}
                  placeholder={t("params.payment_method")}
                >
                  <option value="" />
                  {["Moncash", "Natcash", "Credit Card"].map((option, index) => (
                    <option key={index} value={option}>
                      {option}
                    </option>
                  ))}
                </RHFSelect>

                <RHFTextField
                  name="montant"
                  label="Montant"
                  placeholder="Montant"
                  type="number"
                />
              </Stack>

              {values.method === "Credit Card" && (
                <Stack spacing={2}>
                  <RHFTextField
                    name="cardNumber"
                    label="Card Number"
                    placeholder="Enter your card number"
                    type="text"
                  />
                  <Stack direction="row" spacing={2}>
                    <RHFTextField
                      name="expiryDate"
                      label="Expiry Date (MM/YY)"
                      placeholder="MM/YY"
                    />
                    <RHFTextField
                      name="cvv"
                      label="CVV"
                      placeholder="CVV"
                      type="text"
                    />
                  </Stack>
                </Stack>
              )}

              <Stack direction="row" justifyContent="flex-end" spacing={1.5}>
                <Button color="inherit" variant="outlined" onClick={onCancel}>
                  {t("params.cancel")}
                </Button>
                <LoadingButton
                  loading={isLoading}
                  type="submit"
                  variant="contained"
                >
                  {t("params.recharge")}
                </LoadingButton>
              </Stack>
            </Stack>
          </Box>
        </Card>
      </FormProvider>
    </Collapse>
  );
}
