import React, { useEffect } from "react";
import { capitalCase } from "change-case";
// @mui
import { Container, Tab, Box, Tabs } from "@mui/material";
// routes
import { PATH_DASHBOARD } from "../../routes/paths";
// hooks
import useTabs from "../../hooks/useTabs";
import useSettings from "../../hooks/useSettings";
import useAuth from "../../hooks/useAuth";
// _mock_
import {
  _userPayment,
  _userAddressBook,
  _userInvoices,
  _userAbout,
} from "../../_mock";
// components
import Page from "../../components/Page";
import Iconify from "../../components/Iconify";
import HeaderBreadcrumbs from "../../components/HeaderBreadcrumbs";
import { useTranslation } from "react-i18next";
// sections
import {
  AccountGeneral,
  AccountBilling,
  AccountSocialLinks,
  AccountNotifications,
  AccountChangePassword,
} from "../../sections/@dashboard/user/account";
import { useDispatch, useSelector } from "../../redux/store";
import { useLocation } from "react-router-dom";
// ----------------------------------------------------------------------

export default function UserAccount() {
  const dispatch = useDispatch();
  const location = useLocation();
  const menu = new URLSearchParams(location.search).get("menu");
  const { t } = useTranslation();
  const { user, refresh } = useAuth();

  const { themeStretch } = useSettings();

  const { currentTab, onChangeTab } = useTabs(
    !menu ? "profile" : menu
  );

  const ACCOUNT_TABS = [
    {
      value: "profile",
      icon: <Iconify icon={"mdi:user-edit"} width={20} height={20} />,
      component: <AccountGeneral />,
    },
    {
      value: 'deposit',
      icon: <Iconify icon={'solar:card-bold-duotone'} width={20} height={20} />,
      component: <AccountBilling balance={user?.balance} />,
    },
    {
      value: "change_password",
      icon: <Iconify icon={"ic:round-vpn-key"} width={20} height={20} />,
      component: <AccountChangePassword />,
    },
  ];

  return (
    <Page title="User: Account Settings">
      <Container maxWidth={themeStretch ? false : "lg"}>
        <HeaderBreadcrumbs
          heading={t("params.account")}
          links={[{ name: t("params.account_setting") }]}
        />

        <Tabs
          allowScrollButtonsMobile
          variant="scrollable"
          scrollButtons="auto"
          value={currentTab}
          onChange={onChangeTab}
        >
          {ACCOUNT_TABS.map((tab) => (
            <Tab
              disableRipple
              key={tab.value}
              label={t("params." + tab.value)}
              icon={tab.icon}
              value={tab.value}
            />
          ))}
        </Tabs>

        <Box sx={{ mb: 5 }} />

        {ACCOUNT_TABS.map((tab) => {
          const isMatched = tab.value === currentTab;
          return isMatched && <Box key={tab.value}>{tab.component}</Box>;
        })}
      </Container>
    </Page>
  );
}
