import PropTypes from "prop-types";
import React, { useState } from "react";
import { useSnackbar } from "notistack";
// form
import { useForm } from "react-hook-form";
// @mui
import {
  Stack,
  Card,
  InputAdornment,
  Box,
  Typography,
  IconButton,
  Alert,
  Button,
} from "@mui/material";
import { LoadingButton } from "@mui/lab";
// components
import Iconify from "../../../../components/Iconify";
import { FormProvider, RHFTextField } from "../../../../components/hook-form";
import { CopyToClipboard } from "react-copy-to-clipboard";
import { HOST_CLIENT } from "../../../../config";
import { useTranslation } from "react-i18next";
import axios from "../../../../utils/axios";
// ----------------------------------------------------------------------

const SOCIAL_LINKS = [
  {
    value: "facebookLink",
    icon: <Iconify icon={"eva:facebook-fill"} width={24} height={24} />,
  },
  {
    value: "instagramLink",
    icon: (
      <Iconify icon={"ant-design:instagram-filled"} width={24} height={24} />
    ),
  },
  {
    value: "linkedinLink",
    icon: <Iconify icon={"eva:linkedin-fill"} width={24} height={24} />,
  },
  {
    value: "twitterLink",
    icon: <Iconify icon={"eva:twitter-fill"} width={24} height={24} />,
  },
];

// ----------------------------------------------------------------------

AccountSocialLinks.propTypes = {
  myProfile: PropTypes.object,
  refresh: PropTypes.func,
};

export default function AccountSocialLinks({ myProfile, refresh }) {
  const { enqueueSnackbar } = useSnackbar();
  const { t } = useTranslation();
  const [isCopied, setIsCopied] = useState(false);
  const ref_code = HOST_CLIENT + "/#/auth/register?ref=" + myProfile.ref_code;

  const defaultValues = {
    facebookLink: myProfile.facebookLink,
    instagramLink: myProfile.instagramLink,
    linkedinLink: myProfile.linkedinLink,
    twitterLink: myProfile.twitterLink,
  };

  const methods = useForm({
    defaultValues,
  });

  const {
    handleSubmit,
    formState: { isSubmitting },
  } = methods;

  const onSubmit = async () => {
    try {
      await new Promise((resolve) => setTimeout(resolve, 500));
      enqueueSnackbar("Update success!");
    } catch (error) {
      console.error(error);
    }
  };

  const handleCopyClick = () => {
    setIsCopied(true);
    setTimeout(() => {
      setIsCopied(false);
    }, 3000);
    // Vous pouvez également utiliser une bibliothèque tierce comme "react-copy-to-clipboard"
  };

  const onCreateReferral = async () => {
    try {
      const response = await axios.post("/api/v1/accounts/create-referral");
      enqueueSnackbar(response.data);
      refresh();
    } catch (error) {
      console.error(error);
      enqueueSnackbar(error.response.data, { variant: "error" });
    }
  };

  return (
    <Card sx={{ p: 2 }}>
      <Typography
        variant="overline"
        sx={{ mb: 1, display: "block", color: "text.secondary" }}
      >
        {t("params.referal")}
      </Typography>
      {myProfile?.is_referral ? (
        <CopyToClipboard
          text={ref_code}
          onCopy={() => {
            enqueueSnackbar("Copied with success!");
            handleCopyClick();
          }}
        >
          <Box
            sx={{
              display: "flex",
              flexDirection: "row",
              alignItems: "center",
              justifyContent: "space-between",
              bgcolor: "grey.100",
              borderWidth: isCopied ? 2 : 0,
              borderColor: isCopied ? "success.main" : "grey.100",
              borderStyle: "solid",
              p: 1,
              borderRadius: 1,
            }}
          >
            <Typography variant="subtitle2" sx={{ fontWeight: "bold", color: 'common.black' }}>
              {ref_code}
            </Typography>
            <IconButton>
              <Iconify icon={"eva:copy-fill"} width={24} height={24} />
            </IconButton>
          </Box>
        </CopyToClipboard>
      ) : (
        <Stack spacing={2}>
          <Alert severity="error">{t("params.noreferror")}</Alert>
          <Button onClick={onCreateReferral} variant="contained">
            {t("params.createnow")}
          </Button>
        </Stack>
      )}
      {/* <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
        <Stack spacing={3} alignItems="flex-end">
          {SOCIAL_LINKS.map((link) => (
            <RHFTextField
              key={link.value}
              name={link.value}
              InputProps={{
                startAdornment: <InputAdornment position="start">{link.icon}</InputAdornment>,
              }}
            />
          ))}

          <LoadingButton type="submit" variant="contained" loading={isSubmitting}>
            Save Changes
          </LoadingButton>
        </Stack>
      </FormProvider> */}
    </Card>
  );
}
