import * as Yup from "yup";
import { useSnackbar } from "notistack";
import { useCallback, useEffect, useState } from "react";
// form
import { useForm } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";
// @mui
import { Box, Grid, Card, Stack, Typography } from "@mui/material";
import { LoadingButton } from "@mui/lab";
// hooks
import useAuth from "../../../../hooks/useAuth";
import useIsMountedRef from "../../../../hooks/useIsMountedRef";
// utils
import { fData } from "../../../../utils/formatNumber";
import { fDateOnly } from "../../../../utils/formatTime";
// components
import {
  FormProvider,
  RHFSelect,
  RHFTextField,
  RHFPhoneField,
  RHFUploadAvatar,
  RHFDateField,
} from "../../../../components/hook-form";
import { useDispatch, useSelector } from "../../../../redux/store";
import {
  getGenres,
  getMaritals,
  getCountries,
} from "../../../../redux/slices/generale";
import { useTranslation } from "react-i18next";

// ----------------------------------------------------------------------

export default function AccountGeneral() {
  const { user, update } = useAuth();
  const dispatch = useDispatch();
  const { t } = useTranslation();
  const { countries, genres, maritals } = useSelector(
    (state) => state.generale
  );

  useEffect(() => {
    dispatch(getCountries());
    dispatch(getGenres());
    dispatch(getMaritals());
  }, [dispatch]);

  const { enqueueSnackbar } = useSnackbar();

  const isMountedRef = useIsMountedRef();

  const UpdateUserSchema = Yup.object().shape({
    phone: Yup.string().required("phone is required"),
    first_name: Yup.string().required("first_name is required"),
    last_name: Yup.string().required("last_name is required"),
    // username: Yup.string()
    //   .required("username is required")
    //   .matches(/^[A-Za-z0-9_]{1,15}$/, "your username is incorect"),
    email: Yup.string()
      .email("Email must be a valid email address")
      .required("email is required"),
    photo: Yup.mixed().required("photo is required"),
    country: Yup.string().required("country is required"),
    description: Yup.string().required("description is required"),
  });

  const defaultValues = {
    phone: user?.phone || "",
    first_name: user?.first_name || "",
    last_name: user?.last_name || "",
    username: user?.username || "",
    email: user?.email || "",
    photo: user?.photo || "",
    country: user?.country || "",
    description: user?.description || "",
    city: user?.city || "",
    address: user?.address || "",
    genre: user?.genre || "",
    marital: user?.marital || "",
  };

  const [open, setOpen] = useState(false);

  const methods = useForm({
    resolver: yupResolver(UpdateUserSchema),
    defaultValues,
  });

  const {
    setValue,
    watch,
    setError,
    handleSubmit,
    formState: { isSubmitting },
  } = methods;
  const values = watch();

  const onSubmit = async (data) => {
    try {
      const formData = new FormData();

      Object.entries(values).forEach(([key, value]) => {
        if (!["photo"].includes(key)) {
          if (key === "born_date") {
            formData.append(key, fDateOnly(value));
          } else {
            formData.append(key, value);
          }
        }
      });
      if (open === true) {
        formData.append("photo", data.photo);
      }

      await update(formData);
      enqueueSnackbar("Update success!");
    } catch (error) {
      console.error(error);
      if (isMountedRef.current) {
        setError("afterSubmit", {
          ...error,
          message: error.response.statusText,
        });
        if (error.response.data.phone) {
          setError("phone", { ...error, message: error.response.data.phone });
        }
        if (error.response.data.username) {
          setError("username", {
            ...error,
            message: error.response.data.username,
          });
        }
        if (error.response.data.email) {
          setError("email", { ...error, message: error.response.data.email });
        } else if (error.response.data.non_field_errors) {
          setError("email", {
            ...error,
            message: error.response.data.non_field_errors,
          });
        }
        if (error.response.data.photo) {
          setError("photo", { ...error, message: error.response.data.photo });
        }
      }
    }
  };

  const handleDrop = useCallback(
    (acceptedFiles) => {
      const file = acceptedFiles[0];

      if (file) {
        setValue(
          "photo",
          Object.assign(file, {
            preview: URL.createObjectURL(file),
          })
        );
      }
      setOpen(true);
    },
    [setValue]
  );

  return (
    <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
      <Grid container spacing={3}>
        <Grid item xs={12} md={4}>
          <Card sx={{ py: 10, px: 3, textAlign: "center" }}>
            <RHFUploadAvatar
              name="photo"
              accept="image/*"
              maxSize={3145728}
              onDrop={handleDrop}
              helperText={
                <Typography
                  variant="caption"
                  sx={{
                    mt: 2,
                    mx: "auto",
                    display: "block",
                    textAlign: "center",
                    color: "text.secondary",
                  }}
                >
                  Allowed *.jpeg, *.jpg, *.png, *.gif
                  <br /> max size of {fData(3145728)}
                </Typography>
              }
            />

            {/* <RHFSwitch
              name="isPublic"
              labelPlacement="start"
              label="Public Profile"
              sx={{ mt: 5 }}
            /> */}
          </Card>
        </Grid>

        <Grid item xs={12} md={8}>
          <Card sx={{ p: 3 }}>
            <Box
              sx={{
                display: "grid",
                rowGap: 3,
                columnGap: 2,
                gridTemplateColumns: {
                  xs: "repeat(1, 1fr)",
                  sm: "repeat(2, 1fr)",
                },
              }}
            >
              <RHFTextField
                type="email"
                name="email"
                label={t("field.email")}
              />
              <RHFPhoneField name="phone" label={t("field.phone")} />
              <RHFTextField name="first_name" label={t("field.first_name")} />
              <RHFTextField name="last_name" label={t("field.last_name")} />
              <RHFDateField
                name="born_date"
                maxDate={new Date()}
                label={t("field.born_date")}
              />
              <RHFSelect
                name="genre"
                label={t("field.genre")}
                placeholder={t("field.genre")}
              >
                <option value="" />
                {genres?.map((option) => (
                  <option key={option} value={option}>
                    {t(`genre.${option}`)}
                  </option>
                ))}
              </RHFSelect>

              <RHFSelect
                name="marital"
                label={t("field.marital")}
                placeholder={t("field.marital")}
              >
                <option value="" />
                {maritals?.map((option) => (
                  <option key={option} value={option}>
                    {t(`marital.${option}`)}
                  </option>
                ))}
              </RHFSelect>
              <RHFSelect
                name="country"
                label={t("field.country")}
                placeholder={t("field.country")}
              >
                <option value="" />
                {countries?.map((option) => (
                  <option key={option.id} value={option.id}>
                    {t(`country.${option.name}`)}
                  </option>
                ))}
              </RHFSelect>
              <RHFTextField name="city" label={t("field.city")} />
            </Box>

            <Stack spacing={3} alignItems="flex-end" sx={{ mt: 3 }}>
              <RHFTextField
                name="description"
                multiline
                rows={4}
                label="Description"
              />

              <LoadingButton
                type="submit"
                variant="contained"
                loading={isSubmitting}
              >
                {t("params.save_change")}
              </LoadingButton>
            </Stack>
          </Card>
        </Grid>
      </Grid>
    </FormProvider>
  );
}
